import Footer from "components/Footers/Footer";
import Navbar from "components/Navbars/AuthNavbar";
import React from "react";
import image from "assets/img/slideBackground.jpg";
import { CLINIC_INFO } from "utils/constants";

const services = [{
    description: "The pediatric services offered at our place include treatment for Food allergies, Skin infections, diarrhea,constipation, Nutrition counseling for toddlers and children, Immunization, Pediatric and adolescent endocrinology etc.",
    name: "Child Care"
}, {
    description: "Basic minimum services offered to you under this program will be control of blood sugar, blood pressure, cholesterol and weight as latest guidelines followed worldwide.",
    name: "Diabetics"
}, {
    description: "Asthma is a pulmonary (of the lungs) condition in which your airways narrow and swell, resulting  in production of extra mucus, making it very difficult to breathe. Asthma can start off as being just a minor problem in some people, while in others; it can result in life-threatening Asthma attacks",
    name: "Allergy & Asthma"
},
{
    description: `${CLINIC_INFO.clinicName} provides you with world-class medical expertise, state-of-the-art facilities.`,
    name: "General Medicine"
},
{
    description: `${CLINIC_INFO.clinicName} HI-Tech Labs is most advanced and fully automated diagnostic laboratory and   Diagnostics center with a focus on providing quality at a very affordable cost by making preventive  checkup reachable to billions`,
    name: "X-ray & Laboratory Testing"
}];

export default function Services() {
    return (
        <>
            <Navbar transparent />
            <main>
                <div className="relative pt-16 pb-32 flex content-center items-center justify-center min-h-screen-10">
                    <div
                        className="absolute top-0 w-full h-full bg-center bg-cover"
                        style={{
                            background: 'blue'
                        }}
                    >
                        <span
                            id="blackOverlay"
                            className="w-full h-full absolute opacity-75 bg-black"
                        ></span>
                    </div>
                </div>
                <section className="pb-20 relative block bg-white-300">
                    <div className="container mx-auto px-4 lg:pt-24 lg:pb-64">
                        <div className="flex flex-wrap text-center justify-center">
                            <div className="w-full lg:w-6/12 px-4">
                                <h2 className="text-4xl font-semibold text-black">
                                    Our services keeps you smile
                                </h2>
                                <p className="text-lg leading-relaxed mt-4 mb-4 text-blueGray-400">
                                    Our core values are built around the thought of patient-first
                                </p>
                            </div>
                        </div>
                        <div className="flex flex-wrap mt-12 justify-center">
                            {services.map(s =>
                                <div key={s.name} className="w-full lg:w-4/12 px-4 text-center">
                                    <h6 className="text-xl mt-5 font-semibold text-black">
                                        {s.name}
                                    </h6>
                                    <p className="mt-2 mb-4 text-black">
                                        {s.description}
                                    </p>
                                </div>
                            )}
                        </div>
                    </div>
                </section>
            </main>
            <Footer />
        </>
    );
}
