import { createPopper } from "@popperjs/core";
import React from "react";
import { Link } from "react-router-dom";
import Gallery from "views/Gallery";
import Landing from "views/Landing";
import Services from "views/Services";


export const topdropdown = [{ name: "Home", link: "/", component: Landing }, { name: "Services", link: "/services", component: Services }, { name: "Gallery", link: "/gallery", component: Gallery }];

const PagesDropdown = () => {
  // dropdown props
  const [dropdownPopoverShow, setDropdownPopoverShow] = React.useState(false);
  const btnDropdownRef = React.createRef();
  const popoverDropdownRef = React.createRef();
  const openDropdownPopover = () => {
    createPopper(btnDropdownRef.current, popoverDropdownRef.current, {
      placement: "bottom-start",
    });
    setDropdownPopoverShow(true);
  };
  const closeDropdownPopover = () => {
    setDropdownPopoverShow(false);
  };
  return (
    <>
      {
        topdropdown.map(d => <Link to={d.link} key={d.name} className="lg:text-white lg:hover:text-blueGray-200 text-blueGray-700 px-3 py-4 lg:py-2 flex items-center text-xs uppercase font-bold">
          {d.name}
        </Link>)
      }
    </>
  );
};

export default PagesDropdown;
