import gallery1 from "assets/img/gallery1.jpeg";
import gallery2 from "assets/img/gallery2.jpeg";
import gallery3 from "assets/img/gallery3.jpeg";
import gallery4 from "assets/img/gallery4.jpeg";
import gallery5 from "assets/img/gallery5.jpeg";
import gallery6 from "assets/img/gallery6.jpeg";
import Footer from "components/Footers/Footer";
import Navbar from "components/Navbars/AuthNavbar";
import React from "react";

const gallery = [{ name: "Pharmacy", image: gallery1 },
{ name: "Pre-Consultation Screening ", image: gallery2 },
{ name: "Diabetic Care and Diabetic Foot Care", image: gallery3 },
{ name: "Pharmacy", image: gallery4 },
{ name: "Pharmacy", image: gallery5 },
{ name: "Pharmacy", image: gallery6 }];

export default function Gallery() {
  return (
    <>
      <Navbar transparent />
      <main>
        <div className="relative pt-16 pb-32 flex content-center items-center justify-center min-h-screen-10">
          <div
            className="absolute top-0 w-full h-full bg-center bg-cover"
            style={{
              background: 'blue'
            }}
          >
            <span
              id="blackOverlay"
              className="w-full h-full absolute opacity-75 bg-black"
            ></span>
          </div>
        </div>
        <section className="pb-20 relative block bg-white-300">
          <div className="container mx-auto px-4 lg:pt-24 lg:pb-64">
            <div className="flex flex-wrap text-center justify-center">
              <div className="w-full lg:w-6/12 px-4">
                <h2 className="text-4xl font-semibold text-black mb-4" >
                  Gallery
                </h2>
              </div>
            </div>
            <div className="flex flex-wrap">
              {gallery.map(g => <div className="w-full lg:w-3/12 px-4">
                <h5 className="text-l font-semibold pb-4 text-center">
                  {g.name}
                </h5>
                <div className="hover:-mt-4 relative flex flex-col min-w-0 break-words bg-white w-full mb-6 shadow-lg rounded-lg ease-linear transition-all duration-150">
                  <img
                    alt="..."
                    className="align-middle border-none max-w-full h-auto rounded-lg"
                    src={g.image}
                  />
                </div>

              </div>)}
            </div>
          </div>
        </section>
      </main>
      <Footer />
    </>
  );
}
