import "@fortawesome/fontawesome-free/css/all.min.css";
import "assets/styles/tailwind.css";
import { topdropdown } from "components/Dropdowns/PagesDropdown";
// layouts
import Admin from "layouts/Admin.js";
import Auth from "layouts/Auth.js";
import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Redirect, Route, Switch } from "react-router-dom";
// views without layouts
import Landing from "views/Landing.js";




ReactDOM.render(
  <BrowserRouter>
    <Switch>
      {/* add routes with layouts */}
      <Route path="/admin" component={Admin} />
      <Route path="/auth" component={Auth} />
      {/* add routes without layouts */}
      {/* <Route path="/landing" exact component={Landing} /> */}
      <Route path="/" exact component={Landing} />
      {topdropdown.map(r=> <Route path={r.link} exact component={r.component} />)}
      {/* <Route path="/about" exact component={Landing} />
      <Route path="/services" exact component={Landing} />
      <Route path="/doctors" exact component={Landing} />
      <Route path="/contact" exact component={Landing} /> */}
      {/* <Route path="/profile" exact component={Profile} /> */}
      {/* <Route path="/" exact component={Index} /> */}
      {/* add redirect for first page */}
      <Redirect from="*" to="/" />
    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);
